.App {
  font-family: monospace;
  width: 100%;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  margin: 100px auto;
}
.block {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  margin: 50px 0px;
}
.text {
  font-size: 18px;
  margin: 10px;
}
.code {
  padding: 15px;
  background-color: #282c34;
  border-radius: 10px;
  font-size: 14px;
  color: #ffffff;
}
.example {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
}
.example > .code {
  margin: 10px;
  width: 45%;
  min-width: 300px;
}
.example > .display {
  margin: 10px;
  width: 45%;
  min-width: 300px;
}


.my-tags > .tags {
  background-color: #000;
}
.my-tags > .tags > .tag {
  background-color: #fff !important;
  border-radius: 0px !important;
}
